import React, { useState } from 'react';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { useAuthorization } from '@resultadosdigitais/front-hub/react';

import { DropdownMenu, IconButton, Tooltip } from '@resultadosdigitais/tangram-components';
import { Question, Apps } from '@resultadosdigitais/tangram-react-icons';

import StyledDropdownMenuList from '../../components/DropdownMenuList';
import DropdownMenuItem from '../../components/DropdownMenuItem';
import NavList from '../../components/NavList';
import NavItem from '../../components/NavItem';

const StyledDropdownMenuListBottom = styled(StyledDropdownMenuList)`
  transform: translate3d(0, 12px, 0);
`;

const StyledTooltip = styled(Tooltip)`
  &[aria-expanded='true'] {
    button {
      background-color: var(--color-gray-10);
    }
  }
`;

const StyledIconButton = styled(IconButton.style)`
  padding-left: var(--size-spacing-02);
  padding-right: var(--size-spacing-02);

  &:focus {
    outline: none;
  }

  &:active {
    background-color: var(--color-gray-20);
  }

  svg {
    fill: var(--color-gray-60);
  }
`;

const SecondaryMenu = () => {
  const Allowed = useAuthorization();
  const [selectedChildIndex, setSelectedChildIndex] = useState(null);
  const handleSelectedChildItem = value => setSelectedChildIndex(value);
  const { t: translate, i18n } = useTranslation();
  const isValidForCurrentLanguage = i18n.language !== 'en-US';

  return (
    <NavList data-cypress="navigation" titleLabelledby={translate('secondaryMenu.list.titleLabelledby')} labelledby="secondary-navigation">
      <NavItem secondary>
        <DropdownMenu>
          <DropdownMenu.Trigger id="help-menu">
            <StyledTooltip text={translate('secondaryMenu.nav.help.title')} position={Tooltip.positions.BOTTOM}>
              <StyledIconButton kind={IconButton.kind.TERTIARY}>
                <Question title="Helper Button" titleId="idToHelperIcon" />
              </StyledIconButton>
            </StyledTooltip>
          </DropdownMenu.Trigger>

          <StyledDropdownMenuListBottom>
            <DropdownMenu.Title>{translate('secondaryMenu.nav.help.title')}</DropdownMenu.Title>

            <DropdownMenuItem
              href="/suporte/novo"
              target="_blank"
              selected={selectedChildIndex === 'helpCenter'}
              onClick={() => handleSelectedChildItem('helpCenter')}
            >
              {translate('secondaryMenu.nav.help.child.helpCenter')}
            </DropdownMenuItem>

            {isValidForCurrentLanguage && (
              <DropdownMenuItem
                href="https://resultadosdigitais.com.br/materiais-educativos/"
                target="_blank"
                selected={selectedChildIndex === 'educationalMaterials'}
                onClick={() => handleSelectedChildItem('educationalMaterials')}
              >
                {translate('secondaryMenu.nav.help.child.educationalMaterials')}
              </DropdownMenuItem>
            )}
          </StyledDropdownMenuListBottom>
        </DropdownMenu>
      </NavItem>

      <NavItem secondary>
        <DropdownMenu>
          <DropdownMenu.Trigger id="products-menu">
            <StyledTooltip text={translate('secondaryMenu.nav.products.title')} position={Tooltip.positions.BOTTOM}>
              <StyledIconButton kind={IconButton.kind.TERTIARY}>
                <Apps title="Products Button" titleId="idToProductIcon" />
              </StyledIconButton>
            </StyledTooltip>
          </DropdownMenu.Trigger>

          <StyledDropdownMenuListBottom>
            <DropdownMenu.Title>{translate('secondaryMenu.nav.products.title')}</DropdownMenu.Title>

            {isValidForCurrentLanguage && (
              <DropdownMenuItem
                href="https://crm.rdstation.com.br/"
                target="_blank"
                selected={selectedChildIndex === 'rdStationCRM'}
                onClick={() => handleSelectedChildItem('rdStationCRM')}
              >
                {translate('secondaryMenu.nav.products.child.rdStationCRM')}
              </DropdownMenuItem>
            )}

            <Allowed permission="productMenu:rdStationPartners">
              <DropdownMenuItem
                href="/partners"
                target="_self"
                selected={selectedChildIndex === 'rdStationPartners'}
                onClick={() => handleSelectedChildItem('rdStationPartners')}
              >
                {translate('secondaryMenu.nav.products.child.rdStationPartners')}
              </DropdownMenuItem>
            </Allowed>

            <DropdownMenuItem
              href="https://appstore.rdstation.com/pt-BR"
              target="_blank"
              selected={selectedChildIndex === 'rdStationAppStore'}
              onClick={() => handleSelectedChildItem('rdStationAppStore')}
            >
              {translate('secondaryMenu.nav.products.child.rdStationAppStore')}
            </DropdownMenuItem>

            {isValidForCurrentLanguage && (
              <DropdownMenuItem
                href="https://marketplace.rdstation.com"
                target="_blank"
                selected={selectedChildIndex === 'rdStationMarketplace'}
                onClick={() => handleSelectedChildItem('rdStationMarketplace')}
              >
                {translate('secondaryMenu.nav.products.child.rdStationMarketplace')}
              </DropdownMenuItem>
            )}

            <DropdownMenuItem
              href="https://learn.rduniversity.com.br/community"
              target="_blank"
              selected={selectedChildIndex === 'rdUniversity'}
              onClick={() => handleSelectedChildItem('rdUniversity')}
            >
              {translate('secondaryMenu.nav.products.child.rdUniversity')}
            </DropdownMenuItem>

            {isValidForCurrentLanguage && (
              <>
                <Allowed permission="productMenu:referAndEarn">
                  <DropdownMenu.Divider />

                  <DropdownMenuItem
                    href="https://materiais.resultadosdigitais.com.br/rd-station-influenciadores-advocacy"
                    target="_blank"
                    selected={selectedChildIndex === 'referAndEarn'}
                    onClick={() => handleSelectedChildItem('referAndEarn')}
                  >
                    {translate('secondaryMenu.nav.products.child.referAndEarn')}
                  </DropdownMenuItem>
                </Allowed>
              </>
            )}
          </StyledDropdownMenuListBottom>
        </DropdownMenu>
      </NavItem>
    </NavList>
  );
};

export default SecondaryMenu;
